.Sidebar{
    max-width: 200px;
    height: 100vh;
    overflow: auto;
    display: flex;
    flex-direction: column;
    background-color: rgb(47, 55, 59);
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0.3) 0%, rgba(255, 255, 255, 0.02) 100%);
    position: -webkit-sticky;
    position: sticky;
    top: 0;

}

@media screen and (max-width: 941px) {
    .Sidebar{
        display: none;
     }
}